@import 'fonts';
@import 'layout';



#varioMainTest,
#varioMain{
    font-size: calc(100vw / 120);
    @media ( max-width: 769px ) {
        font-size: calc(100vw / 48);
        overflow-x: hidden;
    }

    .main-font {
        font-family: ProximaNova-Regular;
        font-style: normal;
        font-weight: normal;
    }

    div.modal-box>inner:not(.active) {
        opacity: 0;
    }

    div.modal-box.modal-box-info {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        max-height: 100vh;
        background: #fff;
        padding: 0;

        > .inner {
            position: absolute;
            width: 100%;
            height: -zf-bp-to-em(1050px);
            min-height: -zf-bp-to-em(1050px);
            background: #fff;
            padding: 0;
            margin: 0;

            .information-list {
                position: relative;
                list-style: none;
                position: relative;
                background: #fff;
                height: -zf-bp-to-em(1050px);
                width: 100%;

                .close {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: -zf-bp-to-em(10px);
                    height: -zf-bp-to-em(10px);
                    font-size: -zf-bp-to-em(100px);
                    right: -zf-bp-to-em(5px);
                    top: -zf-bp-to-em(2px);
                    color: #000;
                    cursor: pointer;
                    z-index: 10;
                    opacity: 1;
                    outline: none;
                    @media ( max-width: 769px ) {
                        font-size: -zf-bp-to-em(60px);
                    }
                }

                .information-item {
                    display: none;

                    &.show {
                        display: block;
                    }

                    &[data-item="5"] {
                        padding-top: -zf-bp-to-em(30px);
                    }

                    @mixin info-images {
                        position: absolute;
                        top: 0;
                        left: 0;
                        background-size: cover;
                        background-position: center;
                        width: -zf-bp-to-em(952.34px);
                        height: -zf-bp-to-em(1051px);
                        z-index: 3;

                        @media ( max-width: 769px ) {
                            top: 51.5%;
                            background-size: cover;
                            background-position: center;
                            width: -zf-bp-to-em(768px);
                            height: -zf-bp-to-em(510px);
                        }
                    }

                    @mixin info-items($w: -zf-bp-to-em(534px), $h: -zf-bp-to-em(367px)) {
                        position: absolute;
                        top: 0;
                        right: 0;
                        background-size: cover;
                        background-position: center;
                        width: $w;
                        height: $h;

                        @media ( max-width: 769px ) {
                            display: none;
                        }
                    }

                    .info-text {
                        position: absolute;
                        top: -zf-bp-to-em(91.77px); //99.7px
                        left: 62em;
                        width: -zf-bp-to-em(562px);

                        span {
                            font-weight: 600;
                            font-size: -zf-bp-to-em(130px);
                            line-height: 2em;
                            text-transform: uppercase;
                        }

                        p {
                            margin-left: 3.75em;
                            font-style: normal;
                            // font-weight: 600;
                            font-size: -zf-bp-to-em(20px);
                            line-height: 200%;
                            color: #000000;
                        }
                    }

                    .apple-info {
                        background: url(../img/info/info-block-1.png);

                        @media ( max-width: 769px ) {
                            background: url(../img/info/adapt-info-block-1.png);
                        }

                        @include info-images;
                    }

                    .apple-item {
                        background: url(../img/info/apple-item.png);
                        @include info-items;
                    }

                    .apple-text {
                        color: #FAEBD5;
                    }

                    .aqua-info {
                        background: url(../img/info/info-block-2.png);

                        @media ( max-width: 769px ) {
                            background: url(../img/info/adapt-info-block-2.png);
                        }

                        @include info-images;
                    }

                    .aqua-item {
                        background: url(../img/info/aqua-item.png);
                        @include info-items(-zf-bp-to-em(408px), -zf-bp-to-em(592px));
                    }

                    .aqua-item ~ button {
                        left: -zf-bp-to-em(1088.17px);
                    }

                    .info-text > .aqua-text, .aqua-text {
                        color: #C6E9E0;
                        font-size: -zf-bp-to-em(90px);
                        line-height: 2.5em;
                    }


                    .info-text > .aqua-text ~ p {
                        width: 22.5em;
                        margin-left: 4.75em;
                    }

                    .gorechavka-info {
                        background: url(../img/info/info-block-3.png);

                        @media ( max-width: 769px ) {
                            background: url(../img/info/adapt-info-block-3.png);
                        }

                        @include info-images;
                    }

                    .gorechavka-item {
                        background: url(../img/info/gorechavka-item.png);
                        @include info-items(-zf-bp-to-em(1150px), -zf-bp-to-em(1051px));
                    }

                    .gorechavka-item ~ div {
                        top: 10em;

                        @media ( max-width: 769px ) {
                            top: 2em;
                        }
                    }

                    .gorechavka-item ~ button {
                        top: -zf-bp-to-em(798.14px);  // попробовать без позиционирования по вертикали
                        left: -zf-bp-to-em(1039.17px);
                    }

                    .info-text > .gorechavka-text, .gorechavka-text {
                        color: #E3D4E3;
                        font-size: -zf-bp-to-em(90px);
                        line-height: 1.5em;

                        @media ( max-width: 769px ) {
                            font-size: -zf-bp-to-em(60px) !important;
                        }
                    }

                    .info-text > .gorechavka-text ~ p {
                        width: 22em;
                        margin-top: 2.5em;
                        margin-left: 2.5em;
                    }

                    .cichorium-info {
                        background: url(../img/info/info-block-4.png);

                        @media ( max-width: 769px ) {
                            background: url(../img/info/adapt-info-block-4.png);
                        }

                        @include info-images;
                    }

                    .cichorium-item {
                        background: url(../img/info/cichorium-item.png);
                        @include info-items(-zf-bp-to-em(656px), -zf-bp-to-em(888px));
                    }

                    .cichorium-item ~ div {
                        top: 8em;

                        @media ( max-width: 769px ) {
                            top: 2em;
                        }
                    }

                    .info-text > .cichorium-text, .cichorium-text {
                        color: #BADFF1;
                        font-size: -zf-bp-to-em(90px);
                        line-height: 1.5em;
                    }

                    .info-text > .cichorium-text ~ p {
                        width: 22em;
                        margin-top: 3.5em;
                        margin-left: 3.25em;
                    }

                    .cichorium-text ~ p {
                        @media ( max-width: 769px ) {
                            width: 37em !important;
                        }
                    }

                    .cichorium-item ~ button {
                        top: -zf-bp-to-em(782.14px);
                        left: -zf-bp-to-em(1055.17px);
                    }

                    .beautyfeye-info {
                        background: url(../img/info/info-block-5.png);

                        @media ( max-width: 769px ) {
                            background: url(../img/info/adapt-info-block-5.png);
                        }

                        @include info-images;
                    }

                    .beautyfeye-info ~ div {
                        width: 40em;
                    }

                    .info-text > .beautyfeye-text, .adapt-info-text > .beautyfeye-text  {
                        position: absolute;
                        top: 0;
                        left: 1em;
                        width: 30em;
                        font-weight: 600;
                        font-size: -zf-bp-to-em(50px);
                        line-height: 190%;
                        text-transform: uppercase;
                        color: #00A4AD;

                        b {
                            font-size: 1.5em;
                        }

                        @media ( max-width: 769px ) {
                            top: -.5em;
                            font-size: -zf-bp-to-em(40px);
                            width: 15em;
                            left: 2.5em;

                            b {
                                font-size: 2em;
                            }
                        }
                    }

                    .info-text > .beautyfeye-text ~ p, .adapt-info-text > .beautyfeye-text ~ p {
                        margin-top: 11em;

                        @media ( max-width: 769px ) {
                            margin-top: 7.5em;
                            margin-left: 8em;

                        }
                    }

                    .beautyfeye-descr {
                        position: absolute;
                        top: 40em;
                        left: 66.5em;
                        width: 25em !important;
                        font-family: Arial, sans-serif;
                        font-style: italic;

                        span {
                            font-size: -zf-bp-to-em(20px);
                            line-height: 150%;
                            color: #858585;
                        }

                        @media ( max-width: 769px ) {
                            width: 30em !important;
                            top: 22em;
                            left: 2.5em;
                        }
                    }

                    .beautyfeye-descr ~ button {
                        @media ( max-width: 769px ) {
                            margin-top: 2em;
                        }
                    }

                    .information-item:last-of-type {
                        @media ( max-width: 769px ) {
                            margin-top: 3em;
                        }
                    }

                    .adapt-info-text {
                        display: none;

                        @media ( max-width: 769px ) {
                            position: absolute;
                            // top: -zf-bp-to-em(50px);
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;

                            span {
                                font-size: -zf-bp-to-em(80px);
                                line-height: 190%;
                                text-align: center;
                                text-transform: uppercase;
                            }

                            p {
                                margin-left: 1em;
                                font-size: -zf-bp-to-em(20px);
                                line-height: 200%;
                                width: 36em;
                            }
                        }
                    }

                    .info-button {
                        position: absolute;
                        top: -zf-bp-to-em(782.14px);
                        left: -zf-bp-to-em(1063.17px);
                        width: -zf-bp-to-em(265px);
                        height: -zf-bp-to-em(82.29px);
                        transition: .2s;
                        background: #fff;
                        border: 0.025em solid #000;
                        cursor: pointer;
                        outline: none;

                        span {
                            font-weight: bold;
                        }

                        &:hover {
                            background: #000;
                            color: #fff;
                        }

                        @media ( max-width: 769px ) {
                            top: -zf-bp-to-em(392.40px) !important;
                            left: -zf-bp-to-em(251.5px) !important;
                        }
                    }
                }
            }
        }
    }
    div.modal-box.modal-box-test {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        background: #fff;
        padding: 0;
        > .inner {
            position: absolute;
            width: 100%;
            height: -zf-bp-to-em(1050px);
            min-height: -zf-bp-to-em(1050px);
            background: #86BAD9;
            padding: 0;
            margin: 0;
            @media ( max-width: 769px ) {
                min-height: auto;
                height: auto;
            }

            .experts {
                position: relative;
                width: 100%;
                height: 100%;
                background: url(../img/modal-experts-bg.jpg) no-repeat 50% 50% /cover;
                display: none;

                &.show {
                    display: block;
                }

                .close {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: -zf-bp-to-em(10px);
                    height: -zf-bp-to-em(10px);
                    font-size: -zf-bp-to-em(100px);
                    right: -zf-bp-to-em(5px);
                    top: -zf-bp-to-em(2px);
                    color: #fff;
                    cursor: pointer;
                    z-index: 10;
                    opacity: 1;
                    outline: none;
                    @media ( max-width: 769px ) {
                        font-size: -zf-bp-to-em(80px);
                    }
                }

                .question-list {
                    width: 100%;
                    height: 100%;

                    .question-item {
                        display: none;
                        width: 100%;
                        height: 100%;
                        padding-top: -zf-bp-to-em(150px);
                        display: none;
                        flex-direction: column;
                        align-items: center;
                        @media ( max-width: 769px ) {
                            padding-top: -zf-bp-to-em(70px);
                            padding-bottom: -zf-bp-to-em(50px);
                        }

                        &.show {
                            display: flex;
                        }

                        .title {
                            font-family: ProximaNova-Bold, Arial, sans-serif;
                            font-size: -zf-bp-to-em(40px);
                            line-height: 1.5;
                            color: #fff;
                            text-align: center;
                            text-transform: uppercase;
                            min-height: -zf-bp-to-em(48px);
                            margin-bottom: -zf-bp-to-em(14px);
                            @media ( max-width: 769px ) {
                                font-size: -zf-bp-to-em(25px);
                            }
                        }

                        .wrapper-answer {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            flex-wrap: wrap;
                            margin: 0 auto -zf-bp-to-em(90px);
                            &.wrapper-answer-1 {
                                max-width: -zf-bp-to-em(1420px);
                            }
                            &.wrapper-answer-2 {
                                max-width: -zf-bp-to-em(1100px);
                            }
                            &.wrapper-answer-3 {
                                max-width: -zf-bp-to-em(750px);
                            }
                            &.wrapper-answer-4 {
                                max-width: -zf-bp-to-em(1100px);
                            }
                            &.wrapper-answer-5 {
                                max-width: -zf-bp-to-em(1100px);
                            }
                            &.wrapper-answer-6 {
                                max-width: -zf-bp-to-em(750px);
                            }
                            &.wrapper-answer-7 {
                                max-width: -zf-bp-to-em(750px);
                            }
                            &.wrapper-answer-8 {
                                max-width: -zf-bp-to-em(750px);
                            }

                            .btn-answer {
                                position: relative;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                width: -zf-bp-to-em(320px);
                                max-width: -zf-bp-to-em(320px);
                                height: -zf-bp-to-em(182px);
                                max-height: -zf-bp-to-em(182px);
                                margin: -zf-bp-to-em(19px) -zf-bp-to-em(10px);
                                border: 1px solid #DAEFF9;
                                border-radius: -zf-bp-to-em(20px);
                                box-shadow: 0 0 -zf-bp-to-em(50px) rgba(0, 164, 173, 0.2);
                                background: rgba(255,255,255,.8);
                                transition: .2s;
                                cursor: pointer;
                                padding: -zf-bp-to-em(30px) -zf-bp-to-em(30px) -zf-bp-to-em(60px);
                                outline: none;

                                &.btn-answer-big {
                                    width: -zf-bp-to-em(440px);
                                    max-width: -zf-bp-to-em(440px);
                                }

                                .btn-answer-text {
                                    font-family: ProximaNova-Regular, Arial, sans-serif;
                                    font-size: -zf-bp-to-em(20px);
                                    line-height: 1.4;
                                    color: #000;

                                    &.btn-answer-text-small {
                                        font-size: -zf-bp-to-em(24px);
                                    }

                                    &.btn-answer-text-middle {
                                        font-size: -zf-bp-to-em(30px);
                                    }

                                    &.btn-answer-text-big {
                                        font-size: -zf-bp-to-em(36px);
                                    }
                                }

                                &.active,
                                &:hover {
                                    background: #fff;

                                    &:before {
                                        background: #EA406D;
                                        border-color: #FDC5D4;
                                    }
                                }

                                &:before {
                                    position: absolute;
                                    content: "";
                                    left: 50%;
                                    bottom: -zf-bp-to-em(17px);
                                    width: -zf-bp-to-em(22px);
                                    height: -zf-bp-to-em(22px);
                                    border-radius: 50%;
                                    background: #DADADA;
                                    transition: .2s;
                                    border: -zf-bp-to-em(5px) solid #fff;
                                    transform: translateX(-50%);
                                }
                            }
                        }

                        .btn-more {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background: #fff;
                            width: -zf-bp-to-em(282px);
                            height: -zf-bp-to-em(100px);
                            border-radius: -zf-bp-to-em(20px);
                            box-shadow: 0 0 -zf-bp-to-em(50px) rgba(0, 164, 173, 0.2);
                            cursor: pointer;
                            outline: none;
                            border: none;
                            margin: 0 auto;
                            transition: .2s;

                            .btn-more-text {
                                font-family: ProximaNova-Bold, Arial, sans-serif;
                                font-size: -zf-bp-to-em(24px);
                                line-height: 1.2;
                                color: #000;
                                transition: .2s;
                            }

                            &.disabled {
                                cursor: not-allowed;
                                &:hover {
                                    background: #fff;

                                    .btn-more-text {
                                        color: #000;
                                    }
                                }
                            }

                            &:hover {
                                background: #E83E6D;

                                .btn-more-text {
                                    color: #fff;
                                }
                            }
                        }
                    }
                }
            }

            .independent {
                position: relative;
                width: 100%;
                height: 100%;
                background: #fff;
                display: none;
                @media ( max-width: 769px ) {
                    height: -zf-bp-to-em(1070px);
                }

                &.show {
                    display: block;
                }

                .close {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: -zf-bp-to-em(10px);
                    height: -zf-bp-to-em(10px);
                    font-size: -zf-bp-to-em(100px);
                    right: -zf-bp-to-em(5px);
                    top: -zf-bp-to-em(2px);
                    color: #000;
                    cursor: pointer;
                    z-index: 10;
                    opacity: 1;
                    outline: none;
                    @media ( max-width: 769px ) {
                        font-size: -zf-bp-to-em(60px);
                    }
                }

                .options-list {
                    width: 100%;
                    height: 100%;

                    .options-item {
                        position: relative;
                        display: none;
                        width: 100%;
                        height: 100%;

                        &.show {
                            display: block;
                        }

                        .title {
                            padding-top: -zf-bp-to-em(24px);
                            font-family: ProximaNova-Bold, Arial, sans-serif;
                            font-size: -zf-bp-to-em(30px);
                            line-height: 1.2;
                            color: #B5B5B5;
                            text-align: center;
                            text-transform: uppercase;
                            @media ( max-width: 769px ) {
                                padding-top: -zf-bp-to-em(24px);
                            }
                        }

                        &[data-option="1"]{
                            background: url(../img/test/options-item-1-bg.jpg) no-repeat 50% 50% /cover;
                            @media ( max-width: 769px ) {
                                background: url(../img/test/options-item-1-bg-mob.jpg) no-repeat 50% 50% /cover;
                            }

                            &[data-select="0"] {
                                background: url(../img/test/options-item-1-bg.jpg) no-repeat 50% 50% /cover;
                                @media ( max-width: 769px ) {
                                    background: url(../img/test/options-item-1-bg-mob.jpg) no-repeat 50% 50% /cover;
                                }

                                .next {
                                    display: none;
                                }

                                .select-block {
                                    background: url(../img/test/options-item-1-selected-0.png) no-repeat 50% 50% /contain;
                                }
                            }

                            &[data-select="1"] {
                                background: url(../img/test/options-item-1-bg.jpg) no-repeat 50% 50% /cover;
                                @media ( max-width: 769px ) {
                                    background: url(../img/test/options-item-1-bg-mob.jpg) no-repeat 50% 50% /cover;
                                }
                                .btn-wrapper-select {
                                    .btn-select:nth-child(1) {
                                        .btn-select-text {
                                            color: #EA406D;
                                        }
                                        &:before {
                                            background: #EA406D;
                                            border-color: #FDC5D4;
                                        }
                                    }
                                }
                                .select-block {
                                    background: url(../img/test/options-item-1-selected-1.png) no-repeat 50% 50% /contain;
                                }
                            }

                            &[data-select="2"] {
                                background: url(../img/test/options-item-1-bg-2.jpg) no-repeat 50% 50% /cover;
                                @media ( max-width: 769px ) {
                                    background: url(../img/test/options-item-1-bg-2-mob.jpg) no-repeat 50% 50% /cover;
                                }
                                .btn-wrapper-select {
                                    .btn-select:nth-child(2) {
                                        .btn-select-text {
                                            color: #EA406D;
                                        }
                                        &:before {
                                            background: #EA406D;
                                            border-color: #FDC5D4;
                                        }
                                    }
                                }
                                .select-block {
                                    background: url(../img/test/options-item-1-selected-2.png) no-repeat 50% 50% /contain;
                                }
                            }

                            .btn-wrapper-select {
                                position: absolute;
                                display: flex;
                                flex-direction: column;
                                justify-content: space-between;
                                width: -zf-bp-to-em(190px);
                                height: -zf-bp-to-em(85px);
                                top: -zf-bp-to-em(480px);
                                right: -zf-bp-to-em(290px);
                                @media ( max-width: 769px ) {
                                    flex-direction: row;
                                    right: 50%;
                                    top: -zf-bp-to-em(360px);
                                    width: -zf-bp-to-em(400px);
                                    transform: translateX(50%);
                                }

                                .btn-select {
                                    position: relative;
                                    display: flex;
                                    align-items: center;
                                    background: transparent;
                                    width: 100%;
                                    padding-left: -zf-bp-to-em(56px);
                                    height: -zf-bp-to-em(32px);
                                    border: none;
                                    outline: none;
                                    cursor: pointer;

                                    .btn-select-text {
                                        font-family: ProximaNova-Semibold, Arial, sans-serif;
                                        font-size: -zf-bp-to-em(16px);
                                        text-transform: uppercase;
                                        color: #D5D5D5;
                                        transition: .2s;
                                    }

                                    &:before {
                                        content: "";
                                        position: absolute;
                                        top: 50%;
                                        left: 0;
                                        width: -zf-bp-to-em(20px);
                                        height: -zf-bp-to-em(20px);
                                        background: #D5D5D5;
                                        border: -zf-bp-to-em(5px) solid #EDEDED;
                                        transform: translateY(-50%);
                                        transition: .2s;
                                        border-radius: 50%;
                                    }

                                    &:hover {
                                        .btn-select-text {
                                            color: #EA406D;
                                        }
                                        &:before {
                                            background: #EA406D;
                                            border-color: #FDC5D4;
